.im-icon-button:hover {
    background: -webkit-gradient(linear, left top, right top, from(rgba(48, 70, 105,0.06)), to(rgba(48, 70, 105,0.06)));
    background: -webkit-linear-gradient(left, rgba(48, 70, 105,0.06) 0%, rgba(48, 70, 105,0.06) 100%);
    background: -o-linear-gradient(left, rgba(48, 70, 105,0.06) 0%, rgba(48, 70, 105,0.06) 100%);
    background: linear-gradient(90deg, rgba(48, 70, 105,0.06) 0%, rgba(48, 70, 105,0.06) 100%); 

}


